import React, { Component } from 'react';

class HeaderElement extends Component {

  constructor(props) {
    super(props);

    this.state = {
      label: "Header"
    };
  }

  render() {
    return (
      <h1>
          <span className="editable editable-label">{this.state.label}</span><br />
      </h1>
    );
  }
}

export default HeaderElement;


