import React, { Component } from 'react';
import classNames from 'classnames';
import $ from 'jquery';
import 'jquery-ui/ui/widgets/sortable.js';
import './Sortable.css';
import Element from './Element.js';

class Sortable extends Component {

  constructor(props) {
    super(props);

    this.state = {
      fields: []
    };
  }

  componentDidMount() {
    var that = this;

    $('.sortable').sortable({
      axis: "y",
      cancel: '.editable',
      start: function(e, ui) {

          // creates a temporary attribute on the element with the old index
          $(this).attr('data-previndex', ui.item.index());

      },
      receive: function (event, ui) {
          if (ui.item.attr("id")) {

              var newIndex = parseInt($(this).data("ui-sortable").currentItem.index(), 10),
                  element_id = ui.item.attr("id");

              $(this).removeAttr('data-previndex');
              $(ui.helper).replaceWith("");
              
              let arr = that.state.fields;
              arr.splice(newIndex, 0, element_id);

              that.setState({
                fields: arr
              });

              that.sortableClass = classNames(
                'sortable',
                {'sortable-border': that.state.fields.length === 0}
              );


              //that.elementFocus(newIndex);

          }

      },
      /*update: function (event, ui) {

          if (ui.item.index() !== -1) {

              var newIndex = ui.item.index();

              var oldIndex = parseInt($(this).attr('data-previndex'));
              $(this).removeAttr('data-previndex');

              $(ui.helper).replaceWith("");

              that.fields.splice(newIndex, 0, that.fields.splice(oldIndex, 1)[0]);

              that.elementFocus(newIndex);

              $.ajax({
                url: 'fields/' + oldIndex,
                type: 'POST',
                data: {
                    _token: Laravel.csrfToken,
                    form_id: form_id,
                    fields: JSON.stringify(that.fields),
                    sort_fields: true,
                    rank_order: newIndex,
                    _method: "PATCH"
                },
                success: function(data) {
                  $('.save-time').text(new Date().toLocaleString());
                }
              });
          }
      }*/
    });
  }

  render() {
    //var that = this;

    this.sortableClass = classNames(
      'sortable',
      //{'sortable-border': this.state.fields.length === 0}
    );

    return (
      <div className="sortable-container">
        <div className={this.sortableClass}>
        {this.state.fields.map(function (field,index) {
          return <Element name={field} key={index} />;
        })}
        </div>
      </div>
    );
  }
}

export default Sortable;


