import React, { Component } from 'react';
import './Header.css';

class Header extends Component {
  render() {
    return (
      <header>
        <div className="col-sm-8">
          <a className="logo">Bootstrap Form Builder</a>
        </div>
        <div className="col-sm-4">
          GitHub
        </div>
      </header>
    );
  }
}

export default Header;



